<template>
  <div class="order_nav">
    <el-button
      v-for="(item,index) in navList"
      :key="index"
      @click="$router.push(item.router)"
      type="primary"
      plain
      size="small"
    >{{item.title}}</el-button>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      navList: [
        { title: "已提交工单", router: "/order_submited" },
        { title: "待处理工单", router: "/order_wait" },
        { title: "已接工单", router: "/order_received" },
        { title: "历史工单", router: "/order_history" },
        { title: "所有人工单", router: "/order_all" },
        { title: "工单管理", router: "/order_manage" }
      ]
    };
  },
  computed: {},
  watch: {},
  methods: {},
  created() {},
  mounted() {}
};
</script>
<style scoped>
.order_nav {
  /* background-color: grey; */
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-bottom: 1px solid #dcdfe6;
  margin-bottom: 6px;
}
</style>


